.cards-area-container{
    height: 41rem;
    width: 25rem;
    background-color: rgb(79, 92, 3);
    border-radius: 1rem;
    margin: 0 auto;
    display: grid;
    grid-template-rows: 10% 90%;
    grid-template-areas: "top" "bottom";
    overflow: hidden;
}

.bar1{
    height: 41rem;
    width: 4rem;
    background-color: rgb(29, 26, 26);
    position: relative;
    transform: rotate(45deg);
    top: -30rem;
    left: 10rem;
}

.bar2{
    height: 41rem;
    width: 4rem;
    background-color: rgb(29, 26, 26);
    position: relative;
    transform: rotate(45deg);
    top: -80rem;
    left: 10rem;
}

.bar3{
    height: 41rem;
    width: 4rem;
    background-color: rgb(29, 26, 26);
    position: relative;
    transform: rotate(45deg);
    top: -130rem;
    left: 10rem;
}

.brand-title{
    grid-area: top;
    text-align: center;
    color: aliceblue;
    font-weight: bold;
    font-size: 2rem;
    margin-top: 1rem;
}

.cards-container{
    grid-area: bottom;
    padding: 1rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
