.card{
    height: 90px;
    background-color: rgb(102, 101, 101);
    padding: 10px;
    margin: 10px;
    border-radius: 10px;
    text-align: center;
    position: relative;
    display: flex;
    align-items: center;
}
.card:hover{
    cursor: pointer;
    box-shadow: 0px 0px 9px 4px #f1e980;
}

.card-logo{
    width: 60%;
    height: 60%;
    transition: transform 0.3s ease-out ;
}

.card:hover .card-logo{
    transform: scale(1.15);
}

.list{
    width: 85%;
    transition: transform 0.3s ease-out ;
    text-align: left;
    padding-left: 1rem;
}

